body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header{
  background-color:black;
  color: white;
  padding:20px;
  text-align:center;
 font-size:x-large;
}
.ScoreBoard{
  background-color: yellow;
  display:inline-block;
  height:100px;
  width:100px;
  top:10px;
  position:relative;
  left:85vw;
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

main{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap:25px;
  height:70vh;

}
.Revealer{
  background-color:green;
  display: flex;
  border-radius:50%;
  justify-content:center;
  height:80px;
  width:80px;
 align-items: center;



}
Input{
  padding:10px;
  background-color: aliceblue;
  width:40px;
  outline: none;
  border: 1px solid black;
}
button{
  padding: 8px;
  background-color: black;
  border-radius: 3px;
  color: white;

}
.Suggestionbox{
  background-color: green;
  padding: 10px;
  border: 1px solid black;
  border-radius: 2px;
  color: white;
}